.App {
  text-align: center;
  min-height: 100vh;
}

.App-Loading, .App-Login {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Header Styling */
.App-Header {
  height: 125px;
  padding: 0 18vw;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 2px solid goldenrod;
}

.Header-Navbar {
  display: flex;
}

.Header-Link, .Header-Navbar-Button {
  padding: 5px 10px;
  font-size: 1.6em;
  color: #000;
  background-color: transparent;
  text-decoration: unset;
  border: none;
}

.Header-Navbar-List {
  position: fixed;
  top: 125px;
  display: flex;
  flex-direction: column;
  background-color: whitesmoke;
  border-right: 2px solid goldenrod;
  border-bottom: 2px solid goldenrod;
  border-left: 2px solid goldenrod;
}

.Header-Navbar-Link {
  padding: 5px 20px;
  font-size: 1.45em;
  color: #000;
  background-color: transparent;
  text-decoration: unset;
  border: none;
}

.Header-Navbar-Link-Active {
  color: dimgray;
}

.Header-Link:hover, .Header-Navbar-Button:hover, .Header-Navbar-Link:hover {
  color: goldenrod;
}

/* Cards Component Styling */
.App-Cards {
  padding: 0 15vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Card {
  margin: 20px;
  width: 100px;
  height: 250px;
  background-color: #eaeaea;
}

.Card:hover {
  cursor: pointer;
}

.Card-Title {
  font-size: 1.65em;
}

/* Main & Account Styling */

.Subheader-Nav, .Footer-Nav {
  margin: 0;
  padding: 0 18vw;
  display: flex;
  background-color: #FAFAFA;
}

.Subheader-Nav {
  justify-content: center;
}

.Footer-Nav {
  justify-content: space-around;
}

.Subheader-Nav-Link, .Footer-Nav-Link {
  padding: 5px 10px;
  color: darkblue;
  background-color: transparent;
  border: none;
  list-style: none;
}

.Subheader-Nav-Link:hover, .Footer-Nav-Link:hover {
  color: dimgray;
  cursor: pointer;
}

.Subheader-Nav-Link-Active {
  color: goldenrod;
}

.Subheader-Label, .Footer-Label {
  display: flex;
}

.Subheader-Select, .Footer-Input {
  width: 45px;
  margin: 0 5px;
  text-align: center;
}

select:focus, input:focus {
  border: none;
}

/* Table Component Styling */
table {
  padding: 0 15vw;
  width: 100vw;
  min-height: 70vh;
  background-color: #eaeaea;
}

th, td {
  padding: 3px 10px;
  vertical-align: top;
  text-align: left;
  font-size: 0.8em;
}